*{
    margin:0;
    padding:0;
    text-decoration: none;
}
.container2{
    display:flex;
}
main{
    width: 100%;
    padding: 20px;
}
.sidebar{
    background: whitesmoke;
    color: black;
    height: 100vh;
    width: 200px;
    transition: all 0.5s;
}
.top_section{
    display: flex;
    align-items:center;
    padding:20px 15px;
}
.logo{
    font-size: 30px;
}
.bars{
    display: flex;
    font-size: 25px;
    margin-left: 50px;
}
.link{
    display: flex;
    color: black;
    padding: 10px 15px;
    gap: 15px;
    transition: all 0.5s;
}
.link:hover{
    background:#fa8787;
    color: #000;
    transition: all 0.5s;
}
.active{
    background: #fa8787;
    color: #000;
}
.icon, .link_text{
    font-size: 20px;
}

.wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    grid-auto-rows: minmax(100px, auto);
}

.report{
    background: whitesmoke;
    border-radius: 4%;
    padding: 20px;
    font-size: 20px;
    font-family: sans-serif;
    color: black;
    outline-width: thick;
}

.reportDaily{
    align-items: center;

}


.grafica{
    width: auto;
    height: 50%;
}

.formulario > h1 {
    text-align: center;
}

#cat{
    float: left;
width:100 px;
height:50 px; 
}

.loader {
    border: 4px solid #f3f3f3;
    border-top: 4px solid #3498db;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    animation: spin 1s linear infinite;
    margin: 20px auto;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }